* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
.country-item_list {
  list-style: none;
  display: flex;
  align-items: center;
}

.country-item_list:not(:last-child) {
  margin-bottom: 5px;
}

.country-name-info {
  margin-left: 10px;
}
.country-item {
  list-style: none;
}
.country-item:not(:last-child) {
  margin-bottom: 5px;
}
.country-main-info {
  align-items: center;
}
.country-name {
  font-size: 32px;
  font-weight: 700;
}
.country-parameter {
  font-weight: 700;
}

.country-main-info {
  list-style: none;
}
